import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { Comment } from '../model/comment';

@Injectable()
export class CommentService {

  constructor(private http: HttpClient) { }

  findOne(id: number): Observable<Comment>{
    return this.http.get<Comment>(`/api/comments/${id}`);
  }

  findAll(page: number = 0, size: number = 10, id: number): Observable<Comment[]>{
    const httpParams = new HttpParams().set('page', page.toString()).set('size', size.toString());
    return this.http.get<Comment[]>(`/api/posts/${id}/comments`, {params: httpParams});
  }

  save(comment : Comment, id : number) : Observable<Comment>{
    return this.http.post<Comment>(`/api/posts/${id}/comments`, comment);
  }

  update(comment: Comment, id: number) : Observable<Comment>{
    return this.http.put<Comment>(`/api/comments/${id}`, comment);
  }

}



