import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { Post } from '../model/post';

@Injectable()
export class PostService {

  constructor(private http: HttpClient) { }

  findOne(id: number): Observable<Post>{
    return this.http.get<Post>(`/api/posts/${id}`);
  }

  findAll(page: number = 0, size: number = 10): Observable<Post[]>{
    const httpParams = new HttpParams().set('page', page.toString()).set('size', size.toString());
    return this.http.get<Post[]>('/api/posts/desc', {params: httpParams});
  }

  save(post : Post) : Observable<Post>{
    return this.http.post<Post>('/api/posts', post);
  }

  update(post: Post, id: number) : Observable<Post>{
    return this.http.put<Post>(`/api/posts/${id}`, post);
  }

  findAllPostsOfUsers(page: number = 0, size: number = 10): Observable<Post[]>{
    const httpParams = new HttpParams().set('page', page.toString()).set('size', size.toString());
    return this.http.get<Post[]>('/api/posts/me', {params: httpParams});
  }

  delete(id: number) : Observable<any> {
    return this.http.delete(`/api/posts/${id}`);
  }

  addRating(rating: number, id: number) : Observable<any>{
    return this.http.put(`/api/posts/${id}/rating`, {'rating': rating});

  }

  getSize() : Observable<number> {
    return this.http.get<number>('/api/posts/size');
  }

}



