import { Component, OnInit } from '@angular/core';
import { AuthService } from '../login/auth.service';
import { PostService } from '../posts/post.service';
import { Post } from '../model/post';
import { CommentService } from '../post-details/comment.service';
import { Comment } from '../model/comment';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
})
export class MainComponent implements OnInit {
  posts: Post[];
  newComment: Comment;
  text: string;
  rating: number;
  flag: boolean;
  page: number = 0;
  helper: number = 0;
  maxSize: number;
  size: number = 10;

  constructor(private authService: AuthService,
              private postService: PostService,
              private commentService:CommentService) {
      this.flag = false;
    
  }

  ngOnInit() {
    this.authService.redirect();
    this.getPosts(this.page,this.size);
  
    this.postService.getSize().subscribe(size => {
      this.maxSize = size;
    });
  }

  getPosts(page, size){
    this.postService.findAll(page, size)
    .subscribe(posts => 
      {
        this.posts = posts['content'];
        console.log(posts);
        //this.maxSize = posts['totalPages'];
        //console.log(posts['totalPages']);
        //console.log(this.posts);
      });

  }

  add(id: number) {
    //alert(id);
    this.newComment = {
      text: this.text,
      date: '',
      time: '',
      post: null,
      user: null,
    }
    this.commentService.save(this.newComment, id).subscribe(
      post => { this.getPosts(this.page, this.size); }
    );
  }

  logout(){
    this.authService.logout();

  }

  addRating(id : number){
   // alert(this.rating);
   this.flag = true;

   this.postService.addRating(this.rating, id )
      .subscribe(r => {
        this.getPosts(this.page,this.size);
      })

    
  }

  moveLeft() {
    if(this.page > 0) {
      this.page--;
      this.getPosts(this.page,this.size);
    }
   }
  
   moveRight() {
   // this.helper = this.posts.length;
  //  var sum = this.helper/15;
  //  console.log(sum);

  var size_1 = this.maxSize/this.size;
  
    if (this.page < size_1 - 1) {
      this.page++;
      this.getPosts(this.page, this.size);
    }
   }

  

}



